import common from '../../common/common.js'
	
export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			info:{
				bg_color:'255,255,255',
				bg_alpha:1,
				delta:1,
			},
			scrollTop:0,		//滚动高度
			header_bg_alpha:0,	//头部背景色透明度
			currentPagesLen:1,	//页面数量，看是否返回
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			
			fid:0,					//大类ID
			cid:0,					//小类ID
			tempCurrentCatList:[],	//切换类别时的临时数组
			currentCatList:[],		//当前滚动的类别列表
			bigCatList:[],			//大类别列表
			subCatList:[],			//小类列表
			cindex:-1,				//当前选择中类别
			findex:0,				//弹出层切换类别参数
			scrollLeft:0,			//子栏目滚动距离
			
			keys:'',				//关键字
			
			shopList:[],			//店铺列表
			canbuy:0,
			isall:0,				//是否显示完
			page:0,					//当前页码
			pop_category:false
		};
	},
	onLoad(e) {
		var that = this
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		
		//that.getSearchShopList();		
	},
	
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  
	  that.resetAttr();
	  //搜索店铺列表
	  //that.getSearchShopList();
	  
	  console.log('下拉刷新中...');		  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') {// 来自页面内转发按钮
		  console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		if(userInfo.users_id > 0){
			var shareUrl = '/pages/merchant/merchant?tjuid='+userInfo.users_id
		}else{
			var shareUrl = '/pages/merchant/merchant'
		}
		return {
		  title: '本地优选生活信息平台-凡迪优品',
		  path: shareUrl,
		}
	},
	onReachBottom() {
		var that = this
		that.loadmore();
	},
	methods: {
		inputsearch(e){
		  var that = this
		  var keys = e.detail.value;
		  if (keys != that.keys) {
			that.keys = keys
			that.resetAttr();
		  } else {
		    return false
		  }
		  
		},
		//重置参数
		resetAttr(){
			var that = this
			that.page = 0;
			that.isall = 0;
			that.shopList = []
		},
		
		//加载更多
		loadmore(){
			var that = this
			that.page = that.page+1;
			that.getSearchShopList();
		},
		//搜索店铺列表
		getSearchShopList(){
			var that = this
			var lng = uni.getStorageSync('lng');
			var lat = uni.getStorageSync('lat');
			if(that.keys == ''){
				uni.showToast({
					title:'请输入搜索内容',
					duration:1500,
				})
				return false
			}
			
			uni.request({
				url: common.apiUrl + 'shop/getSearchShopList',
				method: 'GET',
				data: {
					keys:that.keys,
					lng:lng,
					lat:lat,
					page:that.page+1
				},
				success: res => {
					var data = res.data
					console.log(data)
					var shopList = data.shop_list
					for(var i=0;i<shopList.length;i++){
						that.shopList.push(shopList[i])
					}						
					that.isall = data.isall
					that.canbuy = data.canbuy
					console.log("shopList",that.shopList)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//电话联系
		phoneContact(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.commendShopList
			var mobile = e.currentTarget.dataset.mobile
			uni.makePhoneCall({
				phoneNumber: mobile 
			});
		},
		//一键导航
		goaddr(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.shopList
			var lat = shopList[index].shop_lat
			var lng = shopList[index].shop_lng
			
			console.log(parseFloat(lat));
			console.log(parseFloat(lng));
			uni.openLocation({
				latitude: parseFloat(lat),
				longitude: parseFloat(lng),
				success: function () {
					console.log('success');
				}
			});
		},
		navBack(){
			uni.navigateBack({
				delta:1
			})
		},
		navHome(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		},
		//跳转
		jumpUrl(e){
			var that = this
			if(that.canbuy == 1){
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url:url,
				});
			}
			
		}
	}
}
